<template>
	<div v-if="user.username.length != 0">
		<h2>Hello {{ user.username }}</h2>

		<div class="card" style="width: 34rem">
			<div class="card-body" id="offcanvasExampleLabel">
				<h5 class="card-title">My Plekjes</h5>
				<div v-for="(item, index) in user.my_plekjes" class="plekjeslist">
					<div @click="getPlekollection(item.name)" v-bind:class="{ selected: item.selected }">
						{{ item }}
					</div>
				</div>
			</div>
		</div>

        <div class="card" style="width: 34rem">
			<div class="card-body" id="offcanvasExampleLabel">
				<h5 class="card-title">My Plekollections</h5>
				<div v-for="(item, index) in user.my_plekollections" class="plekjeslist">
					<div @click="selectPlekollection(item,index)" v-bind:class="{ selected: index==selected_id }">
						{{ item.name }}
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
var api = require("@/service");

export default {
	data() {
		return {
			user: {
				email: "",
				username: "",
				my_plekjes: [],
				my_plekollections: [],
			},
            selected_id:null
		};
	},
	methods: {
		getUserProfile() {
			const query = JSON.stringify({
				email: this.$store.state.userEmail,
			});
			api.apiCall(
				"POST",
				process.env.VUE_APP_API_URL+"/users/get_one",
				query,
				(e) => {
					let mongodata = JSON.parse(e);
					if (!mongodata) {
						this.$emit("userToBeCreated", true);
					} else {
						this.user = mongodata;
                        this.$store.dispatch("setUserData",mongodata);
					}
				}
			);
		},
        selectPlekollection(item,i){
            this.selected_id = i;
            console.log(item);
        },
	},
	mounted() {
		this.getUserProfile();
	},
};
</script>

<style scoped>
.card{
    margin:auto;
}
</style>