<template>
	<div class="profile">
		<div
			v-if="userToBeCreated |(Object.keys(this.$store.state.userEmail).length !== 0)">
			<div v-if="userToBeCreated">
				<span>Choose a Username: <input v-model="userName" /></span>
				<button
					type="button"
					class="btn"
					@click="checkUserNameAvailability()"
				>
					Save
				</button>

				<div v-if="!userNameAvailable" id="warning">
					<br />
					This username is unavailable
				</div>
			</div>
			<div v-else>
				<UserData
					v-if="Object.keys(this.$store.state.userEmail).length !== 0"
					@userToBeCreated="userToBeCreated = $event"
				/>
			</div>
		</div>
		<div v-else>Loading profile...</div>
	</div>
</template>

<script>
import UserData from "@/components/UserData";
var api = require("@/service");

export default {
	components: {
		UserData,
	},
	data() {
		return {
			userToBeCreated: false,
			userName: "",
			userNameAvailable: true,
		};
	},
	methods: {
		checkUserNameAvailability() {
			const query = JSON.stringify({ username: this.userName });
			api.apiCall(
				"POST",
				process.env.VUE_APP_API_URL + "/users/name_check",
				query,
				(e) => {
					this.userNameAvailable = e === "true";
					if (this.userNameAvailable) {
						this.registerUser();
					}
				}
			);
		},
		registerUser() {
			console.log("creating user");
			const newUser = {
				email: this.$store.state.userEmail,
				username: this.userName,
				my_plekjes: [],
				my_plekollections: [],
			};
			this.userName = "";
			api.apiCall(
				"POST",
				process.env.VUE_APP_API_URL + "/users/new",
				JSON.stringify(newUser),
				(e) => {
					this.userToBeCreated = false;
				}
			);
		},
	},
	beforeCreate() {
		if (Object.keys(this.$store.state.userEmail).length === 0) {
			this.$store.dispatch("auth0getUserEmail");
		}
	},
	mounted() {},
};
</script>

<style>
.profile {
	margin-top: 48px;
}
.btn {
	background-color: #17a2b8;
	color: #f2f2f2;
}
span {
	font-size: 21px;
	text-align: left;
	margin-right: 10px;
}
#warning {
	color: #dc3545;
}
</style>